import Vue from 'vue'
import Router from 'vue-router'

import Dashboard from '../pages/Dashboard.vue'
import About from '../pages/About.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Dashboard',
      component: Dashboard
    },
    {
      path: '/about',
      name: 'About',
      component: About
    }
  ]
})
