import 'material-design-icons-iconfont/dist/material-design-icons.css'

import Vue from 'vue'
import Vuetify from 'vuetify/lib'

import App from './App.vue'

import router from '@/router/'
import store from '@/store/'

import serviceWorkerHelpers from '@/application/helpers/serviceWorkerHelpers'

import 'vuetify/src/stylus/app.styl'
import './main.styl'

import { HSLtoHex } from './application/utils/colour'

const theme = {
  primary: HSLtoHex(208, 100, 40),
  secondary: HSLtoHex(256, 100, 40),
  accent: HSLtoHex(32, 100, 40),
  error: HSLtoHex(0, 100, 40),
  success: HSLtoHex(96, 100, 40),
  warning: HSLtoHex(40, 100, 40)
}

Vue.config.productionTip = false

Vue.use(Vuetify, {
  iconfont: 'md',
  icons: {
    'product': 'explore',
    'support': 'contact_support'
  },
  options: {
    customProperties: true
  },
  theme
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})

serviceWorkerHelpers.install()
