import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    appName: 'Application',
    navigationDrawer: null,
    platformName: 'Platform',
    title: 'Application',
    version: '1.2.3'
  },

  mutations: {
    SET_APPLICATION_TITLE: (state, payload) => {
      state.title = payload
    },

    SET_NAVIGATION_DRAWER: (state, payload) => {
      state.navigationDrawer = payload
    },

    TOGGLE_NAVIGATION_DRAWER: (state) => {
      state.navigationDrawer = !state.navigationDrawer
    }
  },

  actions: {
    CHANGE_APPLICATION_TITLE({ commit }, title) {
      commit('SET_APPLICATION_TITLE', title)
    }
  },

  getters: {
    getAppName: state => state.appName,
    getNavigationDrawer: state => state.navigationDrawer,
    getPlatformName: state => state.platformName,
    getTitle: state => state.title,
    getVersion: state => state.version
  }
})
