<template>
  <v-navigation-drawer
    app
    fixed
    touchless
    v-model="drawer"
  >
    <v-toolbar>
      <v-list>
        <v-list-tile>
          <v-list-tile-avatar>
            <v-icon>account_circle</v-icon>
          </v-list-tile-avatar>
          <v-list-tile-title class="subheading">
            Application
          </v-list-tile-title>
        </v-list-tile>
      </v-list>
    </v-toolbar>
    <v-list>
      <v-list-tile
        v-for="item in items"
        :key="`navigation-drawer-item-key-${item.id}`"
        :to="{ name: item.routeName }"
      >
        <v-list-tile-avatar>
          <v-icon>
            {{ item.icon }}
          </v-icon>
        </v-list-tile-avatar>
        <v-list-tile-content>
          <v-list-tile-title>
            {{ item.title }}
          </v-list-tile-title>
        </v-list-tile-content>
      </v-list-tile>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
  export default {
    name: 'Navigation',

    data: () => ({
      items: [
        {
          icon: '$vuetify.icons.product',
          id: 1,
          routeName: 'Dashboard',
          title: 'Dashboard'
        },
        {
          icon: '$vuetify.icons.support',
          id: 2,
          routeName: 'About',
          title: 'About'
        }
      ]
    }),

    computed: {
      drawer: {
        get () {
          return this.$store.state.navigationDrawer
        },
        set (val) {
          this.$store.commit('SET_NAVIGATION_DRAWER', val)
        }
      }
    }
  }
</script>

